import React, { useState } from "react";
import "@hassanmojab/react-modern-calendar-datepicker/lib/DatePicker.css";
import DatePicker, {
  utils,
} from "@hassanmojab/react-modern-calendar-datepicker";
import "./Calendar.css";
import { useDispatch } from "react-redux";
import * as reservationActions from "../../redux/actions";
import CustomInput from "./Calendar-input.js.js";

const CustomCalendar = (props) => {
  const [selectedDay, setSelectDay] = useState("");
  const dispatch = useDispatch();

  const onChangeHandler = async (value) => {
    const url = window.location.href;
    const id = url.split('=')[1];
    setSelectDay(value);
    dispatch(reservationActions.fetchTableData(value, id));
  };

  return (
    <DatePicker
      value={selectedDay}
      onChange={(value) => onChangeHandler(value)}
      renderInput={CustomInput}
      shouldHighlightWeekends
      minimumDate={utils().getToday()}
    />
  );
};

export default CustomCalendar;
